import xengine from "@zk4/xengine";
// import searchIcon from './assets/search.png';
// import cssFile from './index.css'

xengine.localstorage = xengine.localstorage || {};

// set userDefault
xengine.localstorage.setLocalStorage = function (args) {
  const setConfig = {}
  return new Promise((resolve, reject) => {
    try {
      if (!args) {
        alert('setLocalStorage')
      } else {
        if (args.hasOwnProperty('key') && args.hasOwnProperty('data')) {
          const obj = { ...setConfig, ...args };
          xengine.bridge.call("com.zkty.module.localstorage.setLocalStorage", obj, function (res) {
            if (args.hasOwnProperty('success')) {
              args.success(JSON.parse(res));
            } else {
              resolve(JSON.parse(res));
            }
          })
        } else {
          if (args.hasOwnProperty('fail')) {
            args.fail("setLocalStorage");
          } else {
            reject("setLocalStorage");
          }
        }
      }
    } catch (error) { }
  })
}

// get userDefault
xengine.localstorage.getLocalStorage = function (args) {
  if (!args) {
    alert('getLocalStorage')
  } else {
    const obj = { key: args }
    return xengine.bridge.call("com.zkty.module.localstorage.getLocalStorage", obj, function (res) {
      return JSON.parse(res)
    })
  }
}

// remove userDefault
xengine.localstorage.removeLocalStorage = function (args) {
  const removeConfig = {}
  return new Promise((resolve, reject) => {
    try {
      if (!args) {
        alert('removeLocalStorage')
      } else {
        if (args.hasOwnProperty('key')) {
          const obj = { ...removeConfig, ...args };
          xengine.bridge.call("com.zkty.module.localstorage.removeLocalStorage", obj, function (res) {
            if (args.hasOwnProperty('success')) {
              args.success(JSON.parse(res));
            } else {
              resolve(JSON.parse(res));
            }
          })
        } else {
          if (args.hasOwnProperty('fail')) {
            args.fail("removeLocalStorage");
          } else {
            reject("removeLocalStorage");
          }
        }
      }
    } catch (error) { }
  })
}

export default xengine.localstorage;
